.addItem {
  width: 400px;
  margin: 0 auto;
}
.addItem form {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 50px;
}
.addItem form > input[type="text"] {
  background: #eeeeee;
  flex: 1 1 90%;
  line-height: 50px;
  border-top: none;
  border-right: none;
  border-left: none;
  border-radius: 0;
  border-bottom: 10px solid #bdbdbd;
  padding: 0 8px;
}
.addItem form > button {
  flex: 0 0 10%;
  border: none;
  border-radius: 0;
  background: #424242;
  color: #ffffff;
  transition: 0.15s ease-in-out;
}
.addItem form > button:hover,
.addItem form > button:active,
.addItem form > button:focus {
  background: #757575;
  cursor: pointer;
}
.loadP {
  text-align: center;
}
.itemList {
  width: 400px;
  margin: 0 auto;
}
.itemList > ul {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
}
.itemList > ul > li {
  transition: 0.15s ease-in-out;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  background: #eeeeee;
  height: 50px;
  margin-bottom: 10px;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.itemList > ul > li:hover {
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -7px rgba(0, 0, 0, 0.2);
}
.itemList > ul > li > span {
  line-height: 50px;
}
.itemList > ul > li > button {
  border: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #ef5350;
  color: #ffffff;
  padding: 0 0 2px 0;
}
.itemList > ul > li > button:hover,
.itemList > ul > li > button:active,
.itemList > ul > li > button:focus {
  cursor: pointer;
  background: #f3817f;
}
